.home-wrapper {
  /* background-color: cadetblue; */
  background-color:#222222;
  display: flex;
  height: 90vh;
  /* border: 1px solid green; */
  width: 100%;
  /* margin-top: -40px; */
  /* padding-bottom: 80px; */

  
}
.h-wrap-cont {
  width: 77vw;
  /* border: 1px solid black; */
  height: 700px;
  padding: 10px;
  display: flex;
  margin: auto;
}

.home-left {
  flex: 1;
  /* border: 1px solid blue; */
  padding: 10px;
  display: flex;
  align-items: center;

  justify-content: center;
}

.home-right {
  flex: 1;
  /* border: 1px solid blue; */
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.h-left-wrapper {
  /* border: 1px solid red; */
  height: 620px;
  /* padding: 50px; */
  display: flex;
  flex-direction: column;
  /* padding: 0px 40px; */
}
.h-left-wrapper > div > span {
  font-size: 4rem;
  font-weight: 900;
  text-align: start;
  margin-bottom: 0px;
  color: #68d372;
  /* word-spacing: 0px; */
}
.h-left-wrapper> .h-title-down > p {
  font-size: 16px;
  line-height: 24px;
  margin: 10px 0px 20px;
  font-weight: 400;
  width: 70%;
  color: #6e6e6e;
}
.h-title-head{
    height: 65%;
    /* border: 1px solid green; */
}
.h-title-down{
    height: 30%;
}

.home-buttons {
  display: flex;
  justify-content: space-evenly;
  /* border: 1px solid red; */
  margin: auto;
  width: 100%;
  align-items: center;
  padding: 10px;
  /* justify-content: center; */
}
.h-scroller {
  position: relative;
  display: flex;
  cursor: pointer;
  width: 130px;
  align-items: center;
  justify-content: center;
  color: #6e6e6e;
  font-weight: 600;
  margin: 10px auto;
  margin-bottom: 10px;
  margin-bottom: 25px;
}
